.header__burger-bars {
    width: 100%;
    height: 3px;
    background-color: #fff;
    border-radius: 3px;
}

.header__burger-bars:nth-child(2) {
    transition-duration: .8s;
}

.header__burger-bars:last-child {
    width: 70%;
}

.header__burger-bars:first-child {
    width: 70%;
}
